import { Component, OnInit } from '@angular/core';
// import { ActivatedRoute, Router } from '@angular/router';
import { KeyValue } from '@angular/common';
// import { BackofficeService } from 'src/app/services/backoffice.service';
import { UtilsService } from 'src/app/services/utils.service';
import { listas } from 'src/app/models/back-service.model';
import * as e from 'cors';
import { PromotionsService } from 'src/app/services/promotions.service';
import { Promotion } from 'src/app/models/promotions-models';
import { IDropdownSettings } from 'ng-multiselect-dropdown';
import { co, cp } from '@fullcalendar/core/internal-common';
import { CompanyService } from 'src/app/services/company.service';


@Component({
  selector: 'app-admin-promociones',
  templateUrl: './admin-promociones.component.html',
  styleUrls: ['./admin-promociones.component.css'],
})
export class AdminPromocionesComponent implements OnInit {
  public count: number = 1500;
  public loading: boolean = false;

  constructor(
    // private router: Router,
    private utilsService: UtilsService,
    private companyService: CompanyService,
    // private backofficeService: BackofficeService,
    private promotionService: PromotionsService
  ) {
    this.categoriesHeading = Object.keys(this.listHeading[0]);
  }

  listHeading: {}[] = [
    {
      'Categoría ': '',
      Descripción: '',
      'Fecha de creación': '',
      'Fecha de finalización': '',
      Estatus: '',
      '': '',
    },
  ];

  selectedItems: any[] = [{ id: 1, name: 'Comercio por defecto' }];

  filtro = '';
  renderList: any[] = [];
  isShowDiv: boolean = true;
  categoriesHeading: string[] = [];
  public empresasAll: listas[] | any = [];
  public empresasAux: listas[] | any = [];
  public empresas: listas[] | any = [{ id: null, nombreComercial: 'Seleccione...' }]; //  razonSocial: 'Seleccione...' 
  selectedEmpresa: number = 0;
  promotionList: Promotion[] = [];
  accessToken: string = sessionStorage.getItem('Token')?.toString()!;
  promotionsList: Promotion[] = [];
  public userRole: string = sessionStorage.getItem('Role')?.toString()!;
  public userIdCompany = Number(sessionStorage.getItem('companyId')?.toString()!);
  isAdmin: boolean = false;
  currentCompanyId: number | undefined;
  totalPages = 0;
  pageSize = 15;
  pageNum = 0;
  public dropdownSettings: IDropdownSettings = {
    idField: 'id',
    itemsShowLimit: 1,
    singleSelection: true,
    allowSearchFilter: true,
    textField: 'nombreComercial', //'razonSocial', //'name'
    allowRemoteDataSearch: false,
    closeDropDownOnSelection: true,
    searchPlaceholderText: 'Buscar'
  };
  isBackAction: boolean = false;

  // filter3(query: any) {
  //   return this.empresas.name.filter(function (el: any) {
  //     return el.toLowerCase().indexOf(query.toLowerCase()) > -1;
  //   })
  // }

  // filter2(item: any) {
  //   if (!item)
  //     this.empresas = this.empresasAll;
  //   else {
  //     item = item.toLowerCase();
  //     this.empresas = this.empresasAll.filter((x: any) => x.textField.toLowerCase().indexOf(item) == 0)
  //   }
  // }

  async onFilter($event: any) {
    const value: string = $event.toLowerCase();
    if (value.length >= 3) {
      this.empresas = await this.empresasAll.filter((d: { nombreComercial: any; }, index: any) => {
        // (this.empresasAll.length === (index + 1))  && (this.loading = false);
        if (d.nombreComercial !== null) {
          if (d.nombreComercial.toLowerCase().indexOf(value) !== -1) {
            return true;
          }
        }
        return false;
      })
    }
    // #METODO 2
    // return value ? this.empresasAll.filter( (e: any ) => e.razonSocial.includes(value) ) : this.empresas;
  }

  onItemSelect(item: any) {
    this.selectedEmpresa = item.id;
  }

  onItemDelete(item: any) {
    this.limpiar();
  }

  onDropDownClose() {
    this.empresas = this.empresasAux;
  }

  ngOnInit(): void {
    this.limpiar();
    this.loadData();
    this.currentCompanyId = this.utilsService.getParam('companyId');
    // this.currentCompanyId = this.Activatedroute.snapshot.queryParams['companyId'];
    if (this.currentCompanyId != undefined) {
      this.isBackAction = true;
    }
  }

  originalOrder = (
    a: KeyValue<string, string>,
    b: KeyValue<string, string>
  ): number => {
    return 0;
  };

  public getTypeOf(variable: any): string {
    return typeof variable;
  }

  showActions(event: any, index: number) {
    this.isShowDiv = true;
    var element = document.getElementById('item' + index);
    var listItes = document.getElementsByClassName('actions-disble')[0];
    if (listItes) {
      listItes.classList.remove('actions-disble');
    }
    if ('item' + index !== listItes?.id || !listItes.id) {
      element?.classList.add('actions-disble');
    }
  }


  async loadData() {
    this.loading = true;

    const countCompany = await this.companyService.getListCompany(0, this.count);
    countCompany.success ? this.count = countCompany.count : this.count = 10000;

    this.companyService.getListCompany(0, this.count).then(
      async response => {
        this.empresasAll = response.result.rows;
        this.empresas = countCompany.result.rows;
        this.empresasAux = this.empresas;

        if (this.userRole === 'ROLE_ALIADO_COMERCIAL' && this.userIdCompany != undefined) {
          this.empresas = await this.empresasAll.find((e: any) => e.id === Number(this.userIdCompany))
          this.selectedEmpresa = this.empresas[0].id;
          this.loadCompanyPromotions();
        } else {
          this.isAdmin = true;
        }

        if (this.currentCompanyId != undefined) {
          this.selectedEmpresa = this.currentCompanyId;
          this.loadCompanyPromotions();
        }
      },
      error => {
        this.utilsService.dialog('ERROR', 'Error', error.message.descripcion);
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  async loadCompanyPromotions() {
    try {
      if (this.isAdmin) {
        var response = await this.promotionService.getAllPromotions(
          '',
          null,
          this.pageNum,
          this.pageSize,
          null,
          null,
          this.selectedEmpresa!.toString()
        );
      } else {
        var response =
          await this.promotionService.getAllPromotionsCommercialRole(
            '',
            null,
            this.pageNum,
            this.pageSize,
            null,
            null,
            this.selectedEmpresa!
          );
      }

      if (response?.success === true) {
        this.renderList = [];
        this.promotionList = response.result;
        if (this.isAdmin) {
          response.result.forEach((element: Promotion) => {
            this.renderList.push({
              categoría: element.categoryName,
              descripción: element.description,
              'fecha de creación': element.startDate,
              'fecha de finalización': element.finishDate,
              estatus: element.status,
              id: element.id,
            });
          });

          this.totalPages = Math.ceil(response.count / this.pageSize);
        } else {
          response.result['rows'].forEach((element: Promotion) => {
            this.promotionList = response!.result['rows'];

            this.renderList.push({
              categoría: element.categoryName,
              descripción: element.description,
              'fecha de creación': element.startDate,
              'fecha de finalización': element.finishDate,
              estatus: element.status,
              id: element.id,
            });
          });

          this.totalPages = Math.ceil(response.result.count / this.pageSize);
        }
      } else {
        this.utilsService.dialog(
          'ERROR',
          'Error',
          'Por favor vuelve a intentar más tarde.'
        );
      }
    } catch (error) {
      if (!this.isBackAction) {
        this.utilsService.dialog('ERROR', 'Error', 'No existen promociones');
      } else {
        this.limpiar();
        this.renderList = []
      }
      this.isBackAction = false;
    }
  }

  goToPage(pageName: string) {
    this.utilsService.goToPage(pageName)
    // this.router.navigate([`${pageName}`]);
  }

  gotoCreatePromoForm() {
    this.utilsService.goToPage('/admin/edit-promocion', { companyId: this.selectedEmpresa })
    // this.router.navigate(['/admin/edit-promocion'], {
    //   queryParams: { companyId: this.selectedEmpresa },
    // });
  }

  limpiar() {
    this.selectedEmpresa = this.empresas[0].id;
    this.renderList = [];
  }

  goToEditPromo(item: any) {
    this.promotionList.find((element: Promotion) => {
      if (element.id === item.id) {
        item = element;
      }
    });

    this.utilsService.goToPage('/admin/edit-promocion', { PromoModel: JSON.stringify(item) })
    // this.router.navigate(['/admin/edit-promocion'], {
    //   queryParams: { PromoModel: JSON.stringify(item) },
    // });
  }

  async eliminarPromo(item: any) {
    try {
      const response = await this.promotionService.deletePromotion(item.id);
      if (response?.success === true) {
        this.utilsService.dialog(
          'SUCCESS',
          'Notificación',
          response?.message.description
        );
        this.loadCompanyPromotions();
      } else {
        this.utilsService.dialog(
          'ERROR',
          'Error',
          'Por favor vuelve a intentar más tarde.'
        );
      }
      this.loadCompanyPromotions();
    } catch (error) {
      this.utilsService.dialog('ERROR', 'Error', 'No existen promociones');
    }
  }

  async updateStatusEvent($event: any, id: number) {
    const status = $event.target.value;
    try {
      const response = await this.promotionService.changePromoStatus(
        id,
        status
      );
      if (response?.success === true) {
        this.utilsService.dialog(
          'SUCCESS',
          'Notificación',
          response?.message.description
        );
        this.loadCompanyPromotions();
      } else {
        this.utilsService.dialog(
          'ERROR',
          'Error',
          response?.message.description ??
          'Por favor vuelve a intentar más tarde.'
        );
        this.loadCompanyPromotions();
      }
    } catch (error) {
      this.utilsService.dialog(
        'ERROR',
        'Error',
        'Por favor vuelve a intentar más tarde.'
      );
      this.loadCompanyPromotions();
    }
  }

  onLeft() {
    if (this.pageNum + 1 != 1) {
      this.pageNum--;
      this.renderList.length = 0;
      this.loadCompanyPromotions();
    }
  }

  onRight() {
    if (this.pageNum + 1 != this.totalPages) {
      this.pageNum++;
      this.renderList.length = 0;
      this.loadCompanyPromotions();
    }
  }
}
